import React, { useState } from "react";
import "../css/resister.css"
import axios from "axios";
import {Link} from "react-router-dom";

const Resister = () =>{
    const [user,setuser] = useState({
        name:"",
        email:"",
        password:"",
        repassword:""
    });

    const handlereg = (e) =>{
       const {name,value} = e.target;
       setuser({
        ...user,
        [name]:value
       })
      
    }

    const submitdetailes = () =>{
      const {name,email,password,repassword} = user; 
      if(name  && email  && password  && (password === repassword)){
        axios.post("http://localhost:5000/resister",user)
        .then(res => alert(res.data.message) );
      }else{
        const msg = document.getElementById("errmsg");

        msg.innerHTML="not post successfully";
      }
    }
    // console.log(user);



  return(
    <>
     <div className="maindiv">
       <div className="loginpage">
     <h1 className="heading">Resister</h1>
     <span id="errmsg"></span>
     <input type="text"  value= {user.name} name="name" onChange={handlereg}  placeholder="enter your name" />
       <br/> <br/>
       <input type="email"  value={user.email} name="email" onChange={handlereg} placeholder="enter your email" />
    
       <br/> <br/>
        <input type="password" value={user.password} name="password" onChange={handlereg}  placeholder="enter your password" />
        <br/> <br/>
        <input type="password" value={user.repassword} name="repassword" onChange={handlereg}  placeholder="Re-enter password" />
     
        <div className="submitbtn" onClick={submitdetailes}>sign up</div>
        <div>or</div>
        <Link className="Resisterbtn" to="/login" >Login</Link>
     </div>
     </div>
    </>
  )
}

export default Resister;