import React, { useState } from "react";
import "../css/resister.css"
import axios  from "axios";
import {Link} from "react-router-dom";
import { useNavigate } from "react-router-dom";
import Home from "./Home";

 
  const Login = ({ setloginuser }) =>{

  const history = useNavigate();

    const [user,setuser] = useState({
      
        email:"",
        password:"",
      
    });

    const handlereg = (e) =>{
       const {name,value} = e.target;
       setuser({
        ...user,
        [name]:value
       })
      
    }

    const login = () =>{
      const {email,password} = user;
      if(email && password){
        axios.post("http://localhost:5000/login",user)
        .then((res) =>{
          // alert(res.data.message)
          setloginuser(res.data.user);
          console.log(res.data.user);
          history("/");
          const msg = document.getElementById("passwordmsg");
          if(res.data.message === "password not match"){
           msg.innerHTML=res.data.message;
          }else{
          
            
          }
        })
          
   
        
      }
      else{
        console.log("invalid deailes");
      }
    }

  return(
    <>
    <div className="maindiv">
     <div className="loginpage">
     <h1 className="heading">login</h1>
     
       <input type="email" value= {user.email} name="email" onChange={handlereg} placeholder="enter your email" />
       <br/> <br/>
        <input type="password" value= {user.password} name="password" onChange={handlereg} placeholder="enter your password" />
        <span id="passwordmsg"></span>
        <div className="submitbtn" onClick={login}>submit</div>
        <div>or</div>
        <Link className="Resisterbtn" to="/resister">Resister</Link>
        {/* <button onClick={ () => history("/resister")}>click</button> */}
     </div>
     </div>
    </>
  )
}

export default Login;